// src/components/app/app.tsx
import React, { useEffect } from "react";
import { Routes, Route, useLocation, useNavigate } from "react-router-dom";
import type { Location } from "react-router-dom";

import { ForgotPassword, Login, Main, NotFound, Profile, Register, ResetPassword } from "../../pages";

import OrdersFeed from "../../pages/orders-feed/orders-feed";
import OrderDetails from "../../pages/order-details/order-details";
import OrdersList from "../../pages/orders-list/orders-list";
import Layout from "../layout/layout";

import AdvancedFormat from "dayjs/plugin/advancedFormat";
import dayjs from "dayjs";

import { fetchIngredients } from "../../services/slices/ingredients";

import ProtectedRoute from "../protected-route/protected-route";
import Modal from "../modal/modal";
import IngredientDetails from "../ingredient-details/ingredient-details";

import { useAppDispatch, useAppSelector } from "../../services/hooks";
import {
    selectIsAuthenticated,
    selectCanResetPassword,
    selectAuthInitialized,
    refreshToken,
    getUser
} from "../../services/slices/user";

import AppHeader from "../app-header/app-header";

type LocState = { background?: Location } | null;

dayjs.extend(AdvancedFormat);

const App = () => {
    const dispatch = useAppDispatch();
    const navigate = useNavigate();

    const location = useLocation();
    const state = (location.state as LocState) || null;
    const background = state?.background;

    const isAuthenticated = useAppSelector(selectIsAuthenticated);
    const canResetPassword = useAppSelector(selectCanResetPassword);
    const authReady = useAppSelector(selectAuthInitialized);

    useEffect(() => {
        (async () => {
            //   
            await dispatch(fetchIngredients());
            //   ,   ,  
            const rt = await dispatch(refreshToken());
            if (refreshToken.fulfilled.match(rt)) {
                dispatch(getUser());
            }
        })();
    }, [dispatch]);

    const onModalClose = (href: string) => {
        navigate(href);
    };

    return (
        <>
            <AppHeader />

            {/*   (   ) */}
            <Routes location={background || location}>
                {/*   */}
                <Route path="/" element={<Main />} />

                {/*  : , ,   */}
                <Route
                    element={
                        <ProtectedRoute
                            isAllowed={!isAuthenticated} //       /
                            isReady={authReady}
                            redirectionPath="/"
                            fallback={null}
                        />
                    }>
                    <Route path="/login" element={<Login />} />
                    <Route path="/register" element={<Register />} />
                    <Route path="/forgot-password" element={<ForgotPassword />} />
                </Route>

                {/*   ,    canResetPassword === true */}
                <Route
                    element={
                        <ProtectedRoute
                            isAllowed={canResetPassword}
                            isReady={authReady}
                            redirectionPath="/"
                            fallback={null}
                        />
                    }>
                    <Route path="/reset-password" element={<ResetPassword />} />
                </Route>

                {/*    */}
                <Route
                    element={
                        <ProtectedRoute
                            isAllowed={isAuthenticated} //  
                            isReady={authReady}
                            redirectionPath="/login"
                            fallback={null}
                        />
                    }>
                    <Route path="/profile" element={<Profile />} />
                    <Route path="/profile/orders" element={<OrdersList />} />
                </Route>

                {/*      (  ,  ) */}
                <Route
                    path="/profile/orders/:number"
                    element={
                        <ProtectedRoute
                            isAllowed={isAuthenticated}
                            isReady={authReady}
                            redirectionPath="/login"
                            fallback={null}>
                            <Layout>
                                <OrderDetails />
                            </Layout>
                        </ProtectedRoute>
                    }
                />

                {/*      (  ,  ) */}
                <Route
                    path="/feed/:number"
                    element={
                        <Layout>
                            <OrderDetails />
                        </Layout>
                    }
                />

                {/*    */}
                <Route path="/feed" element={<OrdersFeed />} />

                {/*       */}
                <Route path="/ingredients/:id" element={<IngredientDetails />} />

                {/* 404 */}
                <Route path="*" element={<NotFound />} />
            </Routes>

            {/*       background */}
            {background && (
                <Routes>
                    {/*     */}
                    <Route
                        path="/profile/orders/:number"
                        element={
                            <Modal open={true} onClose={() => onModalClose("/profile/orders")}>
                                <OrderDetails />
                            </Modal>
                        }
                    />

                    {/*     */}
                    <Route
                        path="/feed/:number"
                        element={
                            <Modal open={true} onClose={() => onModalClose("/feed")}>
                                <OrderDetails />
                            </Modal>
                        }
                    />

                    {/*   */}
                    <Route
                        path="/ingredients/:id"
                        element={
                            <Modal open={true} onClose={() => onModalClose("/")}>
                                <IngredientDetails />
                            </Modal>
                        }
                    />
                </Routes>
            )}
        </>
    );
};

export default App;
